<template>
  <header :class="['header', isFixed ? 'fixed-header' : '']">
    <!-- header banner -->
    <transition>
      <div v-if="bannerShow" class="header-banner">
        <a href=""
          >Spring showers are no match for our classic raincoat styles.</a
        >
        <button class="header-banner-close" @click="closeBanner"></button>
      </div>
    </transition>
    <!-- // header banner -->
    <div class="header-main">
      <a class="header-logo" href="#">
        <img
          src="../../assets/img/header/cg-logo-black.svg"
          alt="Canada Goose"
        />
      </a>
      <div class="header-main-left">
        <a class="header-find-store" href=""><span>Find a Store</span></a>
      </div>
      <div class="header-main-right">
        <a class="header-search" @click.prevent="searchShow = !searchShow"
          ><span>Search</span></a
        >

        <!-- 로그인 전 -->
        <!-- <a class="header-sign-in" href=""><span>Sign in</span></a> -->

        <!-- 로그인 후 -->
        <div class="header-sign-out">
          <span>Hello, Canadagoose</span>
          <div class="user-menu">
            <ul>
              <li><a href="">Find An Order</a></li>
              <li><a href="">My Account</a></li>
              <li><a href="">Wish List</a></li>
              <li><a href="">Sign Out</a></li>
            </ul>
          </div>
        </div>

        <a class="header-bag" href="" @mouseover="cartShow = true"
          ><span v-if="cartCount > 0" class="count">{{ cartCount }}</span
          ><span>Bag</span></a
        >
      </div>
      <!-- header-nav -->
      <nav class="header-nav">
        <ul class="header-nav-lv1-list">
          <li
            :class="['header-nav-lv1', lv1[0] ? 'active' : '']"
            @mouseover="openSub(0)"
            @mouseleave="closeSub(0)"
          >
            <a href=""><span>여성</span></a>
            <transition>
              <div v-if="lv1[0]" class="header-nav-sub">
                <!-- <div v-if="true" class="header-nav-sub"> -->
                <HeaderNavSub />
              </div>
            </transition>
          </li>
          <li
            :class="['header-nav-lv1', lv1[1] ? 'active' : '']"
            @mouseover="openSub(1)"
            @mouseleave="closeSub(1)"
          >
            <a href=""><span>남성</span></a>
            <transition>
              <div v-if="lv1[1]" class="header-nav-sub">
                <HeaderNavSub />
              </div>
            </transition>
          </li>
          <li
            :class="['header-nav-lv1', lv1[2] ? 'active' : '']"
            @mouseover="openSub(2)"
            @mouseleave="closeSub(2)"
          >
            <a href=""><span>키즈</span></a>
            <transition>
              <div v-if="lv1[2]" class="header-nav-sub">
                <HeaderNavSubKids />
              </div>
            </transition>
          </li>
          <li
            :class="['header-nav-lv1', lv1[3] ? 'active' : '']"
            @mouseover="openSub(3)"
            @mouseleave="closeSub(3)"
          >
            <a href=""><span>컬렉션</span></a>
            <transition>
              <div v-if="lv1[3]" class="header-nav-sub">
                <HeaderNavSubCollections />
              </div>
            </transition>
          </li>
          <li
            :class="['header-nav-lv1', lv1[4] ? 'active' : '']"
            @mouseover="openSub(4)"
            @mouseleave="closeSub(4)"
          >
            <a href=""><span>휴먼네이쳐</span></a>
            <transition>
              <div v-if="lv1[4]" class="header-nav-sub">
                <HeaderNavSubHumanature />
              </div>
            </transition>
          </li>
          <li class="header-nav-lv1">
            <a href=""><span>비욘드</span></a>
          </li>
        </ul>
      </nav>
      <!-- // header-nav -->

      <!-- HeaderSearch -->
      <HeaderSearch v-model="searchShow" />
      <!-- // HeaderSearch -->
    </div>
    <!-- HeaderCart -->
    <HeaderCart v-model="cartShow" />
    <!-- // HeaderCart -->
  </header>
</template>

<script>
import HeaderNavSub from '../Header/HeaderNavSub.vue' // WOMEN, MEN sub
import HeaderNavSubKids from '../Header/HeaderNavSubKids.vue' // Kids sub
import HeaderNavSubCollections from '../Header/HeaderNavSubCollections.vue' // Collections sub
import HeaderNavSubHumanature from './HeaderNavSubHumanature.vue' // Humanature sub
import HeaderCart from './HeaderCart.vue' // 카트팝업
import HeaderSearch from './HeaderSearch.vue'

export default {
  name: 'Header',
  components: {
    HeaderCart,
    HeaderSearch,
    HeaderNavSub,
    HeaderNavSubKids,
    HeaderNavSubCollections,
    HeaderNavSubHumanature,
  },
  props: {},
  data () {
    return {
      isFixed: false,
      lv1: [false, false, false, false, false],
      bannerShow: true, // 최상단 배너
      searchShow: false, // 검색 팝업
      cartShow: false, // 카트
      cartCount: 1, // 카트 개수
    }
  },
  methods: {
    // 배너 닫기
    closeBanner: function () {
      this.bannerShow = false
    },
    // 서브 메뉴 열기
    openSub: function (n) {
      this.$set(this.lv1, n, true)
    },
    // 서브 메뉴 닫기
    closeSub: function (n) {
      this.$set(this.lv1, n, false)
    },
    // 스크롤 반응
    windowScroll: function (e) {
      if (this.bannerShow && window.scrollY > 44 || !this.bannerShow && window.scrollY > 0) {
        this.isFixed = true
      } else {
        this.isFixed = false
      }
    },
    // 해더 바깥쪽 클릭
    outsideClick: function () {
    },
    // 해더 바깥쪽 오버
    mouseOver: function (e) {
      console.log(e.target)
    }
  },
  created () {
    window.addEventListener('scroll', this.windowScroll)
    document.addEventListener('click', this.outsideClick)
  },
  destroyed () {
    window.removeEventListener('scroll', this.windowScroll)
    document.removeEventListener('click', this.outsideClick)
  },
}
</script>e